.Backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0.4;
  background: var(--sec-color-shades-7);
  transform: translateX(-100%);
  z-index: 150;
  transition: 0.3s;
  backdrop-filter: blur(2px);
}

.Backdrop__active {
  transform: translateX(0);
}